.info__img-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.info__img-container img {
  width: 50%;
  border: 1px solid black;
  border-radius: 100%;
}


.info__main-content {
  background: var(--section-color);
  width: var(--section-width);
}

.info__main-content > h1 {
  text-align: center;
  color: #fff;
  font-size: 1.5rem;
}

.info__main-content > h4 {
  text-align: center;
  color: #F3BF99;
  font-size: 1.125rem;
  margin-top: -0.8rem;
}

.info__main-content_link {
  text-align:center;
  color: #F5F5F5;
  /* font-size: 1rem; */
  margin-top: -12px;
  transition: all 0.25s;
}

a {
  text-decoration: none;
}

a:hover {
  color: #F3BF99;
}

.info__buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.info__buttons-container_button {
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 80%;
  height: 34px; */
  padding: 0.5rem 1rem;
  margin: 0 0.5rem 1rem;  
  border-radius: 25px;
  border: none;
  transition: all 0.25s;
  font-size: 1rem;
}

.info__buttons-container_button:hover {
  cursor: pointer;
  background-color: #F3BF99;
}

.info__buttons-container_button-left {
  background: #5093E2;
  color: white;
  font-weight: 600;
}

.info__buttons-container_button-img {
  margin-right: 8px;
  width: 20px;
}

.info__buttons-container_button-right {
  background: white;
  color: #374151;
  font-weight: 600;
}
:root {
  --bgColor: #374151;
  --section-color: #1A1B21;
  --font-family: "Inter";
  --section-width: 0.75fr;
  --section-padding: 2rem;
  --line-height: 125%;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about__container {
  background: var(--section-color);
  width: var(--section-width);
  line-height: var(--line-height);
}

.about__container > h3 {
  color: #F5F5F5;
  padding: 0 var(--section-padding) 0 var(--section-padding);
  margin-bottom: -0.5rem;
  font-size: 1rem;
}

.about__container > p {
  color: #DCDCDC;
  padding: 0 var(--section-padding) 0 var(--section-padding);
  font-size: 0.9rem;
}


.interests__container {
  background: var(--section-color);
  width: var(--section-width);
  line-height: var(--line-height);
}

.interests__container > h3 {
  color: #F5F5F5;
  padding: 0 var(--section-padding) 0 var(--section-padding);
  margin-bottom: -0.5rem;
  font-size: 1rem;
}

.interests__container > p {
  color: #DCDCDC;
  padding: 0 var(--section-padding) 0 var(--section-padding);
  margin-bottom: -0.5rem;
  font-size: 0.9rem;
}


.footer__container {
  width: var(--section-width);
  border-radius: 0 0 12px 12px;
}

.footer__image-container {
  display: flex;
  padding: 0 2rem;
  margin-top: 4rem;
  justify-content: space-evenly;
  align-items: center;
}

.footer__img {
  /* padding: 1.5rem; */
  width: 30px;
  transition: all 0.25s;
}

.footer__img:hover {
  opacity: 0.75;
}
canvas {
  width: 100% !important;
  /* height: 100% !important; */
}

.App__container {
  display: grid;
  grid-template-columns: var(--section-width) 2fr;
  background-color: var(--section-color);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  font-family: var(--font-family);
}

/* .App__sketch {
  background: url("/src/assets/behance.png");
} */

@media screen and (max-width: 850px) {
  .App__container {
    grid-template-columns: 1fr;
  }
}
